import React from 'react'
import { useState, useEffect } from "react";
import {Link, useLocation} from "react-router-dom"
import "./style.css"
const Navbar = () => {
    const [open, setOpen] = useState(false);
    const [screenWidth, setScreenWidth] = useState(0);
    const location = useLocation();
  
    const trackScreenWidth = () => {
    const width = window.innerWidth;
          setScreenWidth(width);
          if (width > 600) {
            setOpen(true);
          }
        };

    const handleClose = () => {
          if (screenWidth < 600) {
            setOpen(false);
          }
        };
          useEffect(() => {
              trackScreenWidth();
              window.addEventListener("resize", trackScreenWidth);
              return () => window.removeEventListener("resize", trackScreenWidth);
            }, []);
          
  
    return (
        
        <nav className="navbar">
            <div className="nav-wrapper">
                <div className="logo">
                   <Link to="/">
                   <img
                     src={`${process.env.PUBLIC_URL}/ad1.png`} alt="logo"
                    />
                   </Link>
                  </div>
                <div className="list-wrapper">
            <div className="list-wrapper">
        <img
            src="https://github.com/DwinaTech/public-images/blob/main/menu-bars.png?raw=true"
            alt="Menu bars"
            style={{ opacity: !open ? 1 : 0 }}
            onClick={() => {
                setOpen(!open);
              }}
          />
        <img
            src="https://github.com/DwinaTech/public-images/blob/main/cross-menu-icon.png?raw=true"
            alt="Menu cross"
            style={{ opacity: open ? 1 : 0 }}
            onClick={() => {
                setOpen(!open);
              }}
          />

          <ul style={{ left: open ? "0" : "-100vw" }} >
            <li>
              <Link
                 onClick={handleClose}
               style={{ color: location.pathname === "/" && "#4071f4" }}
                to="/"
              
              >
                HOME
              </Link>
            </li>
            <li>
              <Link
                 onClick={handleClose}
                     style={{ color: location.pathname === "/about" && "#4071f4" }}
                to="/about">
                ABOUT
              </Link>
            </li>
            <li>
              <Link
                 onClick={handleClose}
                    style={{ color: location.pathname === "/skills" && "#4071f4" }}
                to="/skills"
                
              >
                SKILLS
              </Link>
            </li>
            <li>
              <Link
                 onClick={handleClose}
                         style={{ color: location.pathname === "/experience" && "#4071f4" }}
                to="/experience"
               
              >
                EXPERIENCE
              </Link>
            </li>
            <li>
              <Link
                 onClick={handleClose}
                  style={{ color: location.pathname === "/contact" && "#4071f4" }}
                to="/contact"
                
              >
                CONTACT
              </Link>
            </li>
          </ul>
        </div>
                </div>
            </div>
        </nav>
    )
}

export default Navbar
